import './App.css';
import React, { useEffect, useState } from 'react';
import * as PIXI from 'pixi.js';
import sun from './assets/images/sun.png'
import aimPng from './assets/images/aim.png'


let sunUrl = './assets/images/sun.png'
let aimUrl = './assets/images/aim.png'

function AimTrainer() {
    const [missNum, setMissNum] = useState(0)
    useEffect(() => {
        let type = "WebGL"
        if (!PIXI.utils.isWebGLSupported()) {
            type = "canvas"
        }
        PIXI.utils.sayHello(type)

        const app = new PIXI.Application();
        const loader = new PIXI.Loader(); // 

        // canvas
        // DOM
        document.getElementById('AimTrainer').appendChild(app.view);

        // 
        // loader.add('aim', aimPng)
        //
        let texture = PIXI.Texture.from(aimPng);
        //
        let aim = new PIXI.Sprite(texture);
        let aims = []

        aim.position.set(1, 1)    //

        const size = 30
        aim.width = size
        aim.height = size
        // 
        const pivotPoint = 15
        aim.pivot.set(pivotPoint, pivotPoint)

        aim.vx = 0;
        aim.vy = 0;

        const bunnyHalfWidth = aim.width / 2
        const bunnyHalfHeight = aim.height / 2
        const spiteMoveWidth = [0, app.renderer.width - aim.width]
        const spiteMoveHeight = [0, app.renderer.height - aim.height]
        const outCheckX = (spite, value) => {
            if (spite.x < spiteMoveWidth[0]) {
                spite.vx = 0;
                spite.x = spiteMoveWidth[0];
            } else if (spite.x > spiteMoveWidth[1]) {
                spite.vx = 0;
                spite.x = spiteMoveWidth[1];
            } else {
                aim.x += aim.vx;
            }
        }
        const outCheckY = (spite, value) => {
            if (spite.y < spiteMoveHeight[0]) {
                spite.vy = 0
                spite.y = spiteMoveHeight[0];
            } else if (spite.y > spiteMoveHeight[1]) {
                spite.vy = 0;
                spite.y = spiteMoveHeight[1];
            } else {
                aim.y += aim.vy
            }
        }


        // bunny
        // app.stage.addChild(aim);
        let times = 0
        // 

        // });
        // app.stage.interactive = true
        function targetMove(delta) {
            outCheckX(aim)
            outCheckY(aim)
        }
        function shoot() {
            const size = 30
            let aim = new PIXI.Sprite(texture);
            aim.position.x = Math.ceil(Math.random() * spiteMoveWidth[1]);
            aim.position.y = Math.ceil(Math.random() * spiteMoveHeight[1]);
            aim.startTime = times
            aim.width = size
            aim.height = size
            aim.visible = true
            aim.interactive = true;
            aim.buttonMode = true;//
            aim.on('click', (e) => {
                aim.visible = false
            })
            app.stage.addChild(aim);
            aims.push(aim);
        }
        app.ticker.add((delta) => {
            // bunny
            times += 1
            if (times % 30 === 0) {
                shoot()
            }
            if (times % 600 === 0) {
                aims = aims.filter(item => item.visible)
            }
            aims.forEach((item) => {
                if (item.startTime <= times - 240) {
                    item.visible = false
                    // setMissNum(missNum + 1)
                }
            })
            // targetMove(delta)
        });
    })
    return (
        <div>
            {/* <div>{missNum}</div> */}
            <div id="AimTrainer">
            </div>
        </div>
    );
}

export default AimTrainer;